@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap');


.not-found {
    width: 40vw;
    min-height: 40vh;
    background-color: rgb(0, 0, 0);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 2px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 61px -8px rgba(38,36,38,1);
    border-radius: 9px 9px 9px 9px;
    z-index: 10;
}

.not-found-text {
    margin-top: 2em;
    font-family: Montserrat;
    font-size: large;
    text-align: center;
    align-items: center;
    color:aliceblue;
}

.ok{
    color: rgb(238, 234, 23);
    font-family: Montserrat;
    background-color: black;
    border-color: rgb(238, 234, 23);
    border-radius: 10px;
    padding: 5px;
    width: 50px;
    cursor: pointer;
    margin-top: 10px;
    outline: none;
}

.ok:hover{
    color: black;
    font-family: Montserrat;
    background-color: rgb(238, 234, 23);
    border-radius: 10px;
    border-color: black;
    padding: 5px;
    width: 50px;
    cursor: pointer;
    margin-top: 10px;
    outline: none;
}