@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');

.dropdown {
    font-family: Poppins;
    background-color: black;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    top: 0;
    height: 80px;
    z-index: 10;
}

.logo {
    cursor: pointer;
    margin-top: 8px;
}

.title{
    font-weight: 400;
    font-family: Lobster Two;
    color: aliceblue;
}


.algobutton, .mazebutton {
    font-family: Montserrat;
    font-size: large;
    cursor: pointer;
    background: none;
    border-style: none;
    border-color: none;
    color: aliceblue;
    outline: none;
}

.buttonDisabled {
    font-family: Montserrat;
    font-size: large;
    cursor: pointer;
    background: none;
    border-style: none;
    border-color: none;
    color: rgb(169, 170, 175);
    outline: none;
}

.algobutton:hover, .mazebutton:hover {
    color: aquamarine;
}

.algobutton, .mazebutton {
    position: relative;
    display: inline-block;
}


ul{
    display: none; 
    position: absolute;
    margin-top: 20px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 0px;
    padding: 0px;  
    background-color:black;
    min-width: 160px;
    z-index: 10;
}
 
li {
    font-family: Montserrat;
    font-size: medium;
    color: aliceblue;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    cursor: pointer;
    z-index: 10;
}

.show {
    display:block;
}

ul li:hover {
    background-color: rgb(11, 223, 152);
    color:black;
}

.visual {
    border: none;
    background-color: rgb(11, 223, 152);
    color: black;
    font-family: Montserrat;
    padding: 10px;
    text-align: center;
    width: 160px;
    border-radius: 5px;
    font-size: medium;
    cursor: pointer;
    outline: none;
}

.gridReset{
    border: none;
    background-color: rgb(11, 223, 152);
    color: black;
    font-family: Montserrat;
    padding: 5px;
    text-align: center;
    border-radius: 5px;
    font-size: medium;
    cursor: pointer;
    outline: none;
    display: flex;
    justify-content: space-around;
    width: 30px;
    margin:5px;
}

.pathReset{
    border: none;
    background-color: rgb(11, 223, 152);
    color: black;
    font-family: Montserrat;
    padding: 5px;
    text-align: center;
    border-radius: 5px;
    font-size: medium;
    cursor: pointer;
    outline: none;
    display: flex;
    justify-content: space-around;
    width: 30px;
    margin:5px;
}

.info-disabled, .info-disabled-path  {
    border: none;
    background-color: rgb(238, 234, 23);
    color: rgb(236, 225, 225);
    font-family: Montserrat;
    padding: 5px;
    text-align: center;
    border-radius: 5px;
    font-size: medium;
    cursor: pointer;
    outline: none;
    display: flex;
    justify-content: space-around;
    width: 30px;
    margin:5px;
}


.disabled {
    border: none;
    background-color: rgb(233, 89, 32);
    color: rgb(236, 225, 225);
    font-family: Montserrat;
    padding: 10px;
    text-align: center;
    width: 160px;
    border-radius: 5px;
    font-size: medium;
    cursor: pointer;
    outline: none;
}

.visual:hover, .pathReset:hover, .gridReset:hover {
    background-color: rgb(28, 199, 113);
    color: black;
    border-radius: 5px;
}

.infos{
    display: flex;
    justify-content: space-around;
    margin: 5px;
}