@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');


.dialog-box {
    height: 100vh;
    width: 100vw;
    position: relative;
    background-color: rgb(29, 29, 29);
}

.special{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.msgs {
    font-family: Poppins;
    font-size: 30px;
    color: aliceblue;
    text-align: center;
    align-items: center;
}

.msg {
    font-family: Poppins;
    font-size: 25px;
    color: aliceblue;
    text-align: center;
    align-items: center;
}

.highlight {
    font-family: Poppins;
    font-size: 60px;
    color: rgb(11, 223, 152);
    text-align: center;
    align-items: center;
}

.imp-note{
    font-family: Poppins;
    font-size: 20px;
    color: rgb(189, 230, 95);
}

.buttons  {
    width: 100vw;
    bottom: 10px;
    position: absolute;
    outline: none;
    display: flex;
    justify-content: space-between;
}

.skip {
    border-color: rgb(29, 29, 29);
    outline: none;
    font-family: Montserrat;
    font-size: large;
    width: 100px;
    background-color: rgb(11, 223, 152);
    margin-left: 15px;
    margin-top: 15px;
    padding: 20px;
    cursor: pointer;
    border-radius: 10px;
}

.next {
    border-color: rgb(29, 29, 29);
    outline: none;
    font-size: large;
    width: 100px;
    font-family: Montserrat;
    background-color: rgb(11, 223, 152);
    margin-right: 15px;
    margin-top: 15px;
    padding: 20px;
    cursor: pointer;
    border-radius: 10px;
}

.skip:hover, .next:hover {
    background-color: rgb(29, 29, 29);
    color:rgb(11, 223, 152);
    border: rgb(11, 223, 152) 1px solid;
}

.available {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
}

.algo p, .mazealgo p {
    text-align: left;
    padding: 50px;
}

.icons{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 80vh;
    width: 30vh;
}

.text{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 80vh;
}

.menu{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 1em;

}

.option p {
    text-align: left;
}

.background-blocked {
    background-color: rgb(238, 234, 23);
    width: 40px;
    height: 40px;
    padding: 10px;
    border-radius: 10px;
}

.background{
    background-color: rgb(11, 223, 152);
    width: 40px;
    height: 40px;
    padding: 10px;
    border-radius: 10px;
}

