@import url('https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');

.fOf{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.back-to-home{
    background-color: rgb(3,191,166);
    outline:none;
    font-family: Poppins;
    font-weight: bold;
    font-size: large;
    color:aliceblue;
    border: none;
    padding: 15px;
    margin-top: 20px;
    border-radius: 10px;
    cursor: pointer;
}

.back-to-home:hover{
    border: rgb(3,191,166) 1px solid;
    background-color: white;
    outline:none;
    font-family: Poppins;
    font-weight: bold;
    font-size: large;
    color:rgb(3,191,166);
    padding: 15px;
    margin-top: 20px;
    border-radius: 10px;
}