@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');

.mainGrid{
  min-height: 100vh;
}

.grids{
    outline: 2px solid rgb(241, 242, 245);
    margin-top: -20px;
    cursor: pointer;
} 

.row{
    height: 25px;
}

.cell{
    height: 25px;
    width: 25px;
    outline: 1px solid rgb(241, 242, 245);
    display: inline-block;
}
  
.source{
    height: 25px;
    width: 25px;
    display: inline-block;
    background-image: url('./source.svg');
    background-size: contain;
 }

.dest{
  height: 25px;
  width: 25px;
  display: inline-block;
  background-image: url('./dest.svg');
  background-size: contain;
}
  
@keyframes example {
    from {
      transform: scale(0.1);
      background-color: rgba(74, 12, 218, 0.89);
    }
    to {
      transform: scale(1);
      background-color: rgb(116, 213, 226);
    }
}
 
.visited {
    background-color: rgba(255, 255, 255, 0.89);
    animation-name: example;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    width: 25px;
    height: 25px;
    outline: 1px rgba(255, 255, 255, 0.541);
    outline-style: solid;
    display: inline-block;
}
  
@keyframes makepath {
    from {
      background-color: rgb(245,184,95);
    }
    to {
      background-color: rgb(180, 228, 49);
    }
}
 
.retrack{
    animation-name: makepath;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-out;
    width: 25px;
    height: 25px;
    display: inline-block;
}

@keyframes makewall {
  from {
    background-color: rgba(157, 158, 147, 0.856);
  }
  to {
    background-color: rgb(39, 39, 39);
  }
}

.wall{
  background-color: rgb(177, 255, 229);
  animation-name: makewall;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  width: 25px;
  height: 25px;
  display: inline-block;
}

.distance {
  font-family: Poppins;
  font-size: xx-large;
  color: rgb(0, 0, 0);
  text-align: center;
  margin: 1.5em
}