@-webkit-keyframes example {
    from {
      -webkit-transform: scale(0.1);
              transform: scale(0.1);
      background-color: rgba(74, 12, 218, 0.89);
    }
    to {
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(116, 213, 226);
    }
}
  
@keyframes example {
    from {
      -webkit-transform: scale(0.1);
              transform: scale(0.1);
      background-color: rgba(74, 12, 218, 0.89);
    }
    to {
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(116, 213, 226);
    }
}
 
.visited {
    background-color: rgba(255, 255, 255, 0.89);
    -webkit-animation-name: example;
            animation-name: example;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    width: 25px;
    height: 25px;
    outline: 1px rgba(255, 255, 255, 0.541);
    outline-style: solid;
    display: inline-block;
}
  
@-webkit-keyframes makepath {
    from {
      background-color: rgb(245,184,95);
    }
    to {
      background-color: rgb(180, 228, 49);
    }
}
  
@keyframes makepath {
    from {
      background-color: rgb(245,184,95);
    }
    to {
      background-color: rgb(180, 228, 49);
    }
}
 
.retrack{
    -webkit-animation-name: makepath;
            animation-name: makepath;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    width: 25px;
    height: 25px;
    display: inline-block;
}

@-webkit-keyframes makewall {
  from {
    background-color: rgba(157, 158, 147, 0.856);
